body {
    font-family: 'Arial', sans-serif;
    background: #e6e6e6;
    padding: 2em;
}
.gameBoard {
    margin: 0 auto;
    width: 600px;
    height: 600px;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(3, 1fr);
    grid-gap: 1em;
    background: #d3d3d3;
    border-radius: 5px;
    padding: 1em;
    margin-top: 5vh;
  }
  
  .gridItem {
    width: 100%;
    height: 100%;
    border-radius: 5px;
    background: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 2em;
    font-weight: bold;
    color: #008040;
}
  .gridItem :hover {
    background: #008040;
    color: #fff;
    cursor: pointer;
}
.gameOver {
  background-color: #fff;
  width: 200px;
  padding: 20px 10px;
  border-radius: 10px;
  text-align: center;
  position: absolute;
  top: 10%;
  left: 50%;
  transform: translate(-50%, -50%);
}

h1 {
  font-size: 20px;
  font-weight: bold;
  margin-bottom: 10px;
}

button {
  border: none;
  background-color: #333;
  color: #fff;
  padding: 8px 15px;
  font-size: 14px;
  font-weight: bold;
  border-radius: 6px;
}